const BaseModule = {
    namespaced: true,

    state: {
        saveReady: null,
        
        listPenyiar: null,
        listPenyiarAllChecked: null,
        listDataDinas: null,
        allChecked: null,
        lastSurat: null,
        lastTelepon: null,
        searchMain: null,

        titleTeleponSurat: null,
        listJenisTanggapan: null,
        listJenisTanggapanAllChecked: null,
        listTeleponSurat: null,

        detailKontak: null,
        listPenyiarDetailKontak: null,

        doc: null,
    },

    getters: {
        saveReady: (state) => state.saveReady,
        
        listPenyiar: (state) => state.listPenyiar,
        listPenyiarAllChecked: (state) => state.listPenyiarAllChecked,
        listDataDinas: (state) => state.listDataDinas,
        allChecked: (state) => state.allChecked,
        lastTelepon: (state) => state.lastTelepon,
        lastSurat: (state) => state.lastSurat,
        searchMain: (state) => state.searchMain,

        titleTeleponSurat: (state) => state.titleTeleponSurat,
        listJenisTanggapan: (state) => state.listJenisTanggapan,
        listJenisTanggapanAllChecked: (state) => state.listJenisTanggapanAllChecked,
        listTeleponSurat: (state) => state.listTeleponSurat,

        detailKontak: (state) => state.detailKontak,
        listPenyiarDetailKontak: (state) => state.listPenyiarDetailKontak,
        
        doc: (state) => state.doc
    },

    mutations: {
        saveReady: (state, payload) => state.saveReady = payload,
        
        listPenyiar: (state, payload) => state.listPenyiar = payload,
        listPenyiarAllChecked: (state, payload) => state.listPenyiarAllChecked = payload,
        listDataDinas: (state, payload) => state.listDataDinas = payload,
        allChecked: (state, payload) => state.allChecked = payload,
        lastTelepon: (state, payload) => state.lastTelepon = payload,
        lastSurat: (state, payload) => state.lastSurat = payload,
        searchMain: (state, payload) => state.searchMain = payload,

        titleTeleponSurat: (state, payload) => state.titleTeleponSurat = payload,
        listJenisTanggapan: (state, payload) => state.listJenisTanggapan = payload,
        listJenisTanggapanAllChecked: (state, payload) => state.listJenisTanggapanAllChecked = payload,
        listTeleponSurat: (state, payload) => state.listTeleponSurat = payload,

        detailKontak: (state, payload) => state.detailKontak = payload,
        listPenyiarDetailKontak: (state, payload) => state.listPenyiarDetailKontak = payload,

        doc: (state, payload) => state.doc = payload
    },
};

export default BaseModule;