<template>
  <div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row class="py-4" no-gutters style="position: relative">
          <v-col class="d-flex justify-center" style="position: absolute"><h2 class="white--text">{{
              titleTeleponSurat
            }}</h2></v-col>
          <v-col
              class="d-flex justify-space-between" style="z-index: 1; position: relative;">
            <v-icon
                color="white"
                dense
                style="cursor:pointer; "
                x-large
                @click="backToHome"
            >mdi-chevron-left
            </v-icon>
            <h2 class="white--text" style="cursor:pointer;" @click="backToHome">Back</h2>
            <DialogFilter
                :filter-all="filterData"
                :filter-selected="filterData"
            />

          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col>
            <v-text-field v-model="search" background-color="6c9d00" class="rounded-l text--white mainSearch" clearable
                          dense
                          filled label="Search"
                          solo-inverted
                          @input="filterData()" @click:clear="filterData">
              <template v-slot:prepend-inner="">
                <v-icon color="white">mdi-magnify</v-icon>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-card v-for="teleponSurat in listTeleponSurat" :key="teleponSurat.key" flat>
        <v-container v-if="teleponSurat && teleponSurat.namaPemilikusaha" fluid>
          <v-row @click="goToDetailKontak(teleponSurat)" style="cursor: pointer;">
            <v-col>
              <v-row>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-medium">
                  {{ teleponSurat.namaPemilikusaha }}</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-regular">
                  {{ formatDate(teleponSurat.tanggalDikerjakan) }}</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="grey--text font-weight-regular">
                  {{ teleponSurat.jenisTanggapan }}</h4>
                </v-col>
              </v-row>
            </v-col>
            <v-col class="ml-auto mr-2" style="text-align: end">
              <v-avatar v-if="teleponSurat.jenis.toUpperCase() === 'TELEPON' " 
                  color="#7fb901"
                        size="48"
              >
                <v-icon >mdi-phone-in-talk</v-icon>
              </v-avatar>
              <v-avatar v-if="teleponSurat.jenis.toUpperCase() === 'SURAT' "
                        color="orange darken-2"
                        size="48"
              >
                <v-icon >mdi-email-fast-outline</v-icon>
              </v-avatar>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-container>
      </v-card>
      <div style="height: 50px"></div>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import DialogFilter from "@/views/ListTeleponSurat/DialogFilter";
import {mapGetters} from "vuex";

export default {
  name: "Home",
  data() {
    return {
      listDataDinas: [],
      search: "",
    }
  },
  computed: {
    ...mapGetters({
      lastTelepon: 'base/lastTelepon',
      lastSurat: 'base/lastSurat',
      listPenyiar: 'base/listPenyiar',
      listTeleponSurat: 'base/listTeleponSurat',
      titleTeleponSurat: 'base/titleTeleponSurat',
      listJenisTanggapan: `base/listJenisTanggapan`,

    })
  },
  // watch: {
  //     listPenyiar() {
  //       console.log("gk kelamaan apa ya")
  //       this.filterData();
  //   }
  // },
  methods:
      {
        goToDetailKontak(kontak) {
          this.$store.commit("base/detailKontak", kontak);
          this.$router.push('/detail-kontak')
        },
        backToHome() {
          this.$router.push('/')
        },
        filterData() {
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let penyiarTerpilih = this.listPenyiar ? this.listPenyiar.filter(x => x.isChecked).map(x => x.name) : [];
            let jenisTanggapanTerpilih = this.listJenisTanggapan ? this.listJenisTanggapan.filter(x => x.isChecked).map(x => x.name) : [];
            let loadDataDinasPenyiar = [];
            let loadDataDinasTanggapan = [];
            this.storageSave("listTeleponSurat", loadDataDinas.slice(0, 100));
            if (this.search) {
              loadDataDinas = loadDataDinas.filter(x => x.namaPemilikusaha.toLowerCase().includes(this.search.toLowerCase()));
            }
            if (penyiarTerpilih.length > 0)
              penyiarTerpilih.forEach(x => {
                loadDataDinasPenyiar = [...new Set([...loadDataDinasPenyiar, ...loadDataDinas.filter(xx => xx.name === x)])];
              });
            else loadDataDinasPenyiar = loadDataDinas

            if (jenisTanggapanTerpilih.length > 0) {
              jenisTanggapanTerpilih.forEach(x => {
                loadDataDinasTanggapan = [...new Set([...loadDataDinasTanggapan, ...loadDataDinasPenyiar.filter(xx => xx.jenisTanggapan === x)])];
              });
            }
            else loadDataDinasTanggapan = loadDataDinasPenyiar
            loadDataDinasTanggapan = loadDataDinasTanggapan.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            })
            
            let teleponSurat = this.titleTeleponSurat ? this.titleTeleponSurat : "TELEPON";
            this.$store.commit("base/listTeleponSurat", loadDataDinasTanggapan.filter(x => x.jenis.toLowerCase() === teleponSurat.toLowerCase()).slice(0, 100));
          }
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
      },
  async mounted() {
    moment.locale('id');
      this.filterData();
  },
  
  components: {DialogFilter}
};
</script>
<style scoped>
html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}
</style>
