<template>
  <v-dialog max-width="600" scrollable transition="dialog-bottom-transition">
    <template v-slot:activator="{ on, attrs }">
      <v-icon class="ml-auto" color="white" v-bind="attrs" v-on="on">mdi-filter-outline</v-icon>
    </template>
    <template v-slot:default="dialog">
      <v-card class="rounded-t-lg">
        <v-toolbar color="#7fb901" dark dense flat>
          <v-container>
            <v-row class="" style="position: relative">
              <v-col class="" style="z-index: 1; position: relative"><span style="cursor:pointer"
                                                                           @click="dialog.value = false">Done</span>
              </v-col>
              <v-col class="d-flex justify-center font-weight-medium" style="position: absolute">Filter And Sort</v-col>
            </v-row>
          </v-container>
        </v-toolbar>
        <v-card-text>
          <div class="my-2 font-weight-bold black--text">Penugasan Ke</div>
          <div class="my-2 font-weight-medium black--text d-flex justify-space-between" @click="allClicked">
            <div>All</div>
            <div>
              <v-icon v-if="allChecked" color="#7fb901">mdi-check</v-icon>
            </div>
          </div>
          <v-divider></v-divider>
          <div v-for="filter in listFilter" :key="filter.name">
            <div class="my-2 font-weight-medium black--text d-flex justify-space-between"
                 @click="filterClicked(filter)">
              <div>{{ filter.name }}</div>
              <div>
                <v-icon v-if="filter.isChecked" color="#7fb901">mdi-check</v-icon>
              </div>
            </div>
            <v-divider></v-divider>
          </div>
        </v-card-text>
        <v-card-actions class="justify-end"></v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>
<script>import {mapGetters} from "vuex";

export default {
  name: 'DialogFilter',
  props: {
    filterSelected: {
      type: Function
    },
    filterAll: {
      type: Function
    },

  },
  computed: {
    ...mapGetters({
          allChecked: `base/listPenyiarAllChecked`,
          listFilter: `base/listPenyiar`,
        }
    )
  },
  methods: {
    filterClicked(filter) {
      filter.isChecked = !filter.isChecked;
      this.$store.commit(`base/listPenyiarAllChecked`, false);
      this.$store.commit(`base/listPenyiar`, this.listFilter);
      this.filterSelected();
    },
    allClicked() {
      this.listFilter.forEach(x => x.isChecked = false);
      this.$store.commit(`base/listPenyiarAllChecked`, true);
      this.$store.commit(`base/listPenyiar`, this.listFilter);
      this.filterAll();
    },
  },
  mounted() {
    // console.log(this.listFilter)
  }

}</script>
<style scoped>html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}</style>