<template>
  <v-dialog v-model="dialog"
            max-width="600" scrollable transition="dialog-bottom-transition">
    <template v-slot:activator="{ on, attrs }">
      <v-combobox v-model="detailKontak.name" dense
                  label=""
                  outlined
                  v-bind="attrs"
                  v-on="on"
      >
      </v-combobox>
    </template>
    <template v-slot:default="dialog">
      <v-card class="rounded-t-lg">
        <v-toolbar color="#7fb901" dark dense flat>
          <v-container>
            <v-row class="" style="position: relative">
              <v-col class="" style="z-index: 1; position: relative"><span style="cursor:pointer"
                                                                           @click="dialog.value = false">Done</span>
              </v-col>
              <v-col class="d-flex justify-center font-weight-medium" style="position: absolute">Pilihlah Nama
                Saudara/i:
              </v-col>
            </v-row>
          </v-container>
        </v-toolbar>
        <v-card-text>
          <div v-for="filter in listFilter" :key="filter.name">
            <div v-if="filter" class="my-2 font-weight-medium black--text d-flex justify-space-between"
                 @click="filterClicked(filter)">
              <div>{{ filter.name }}</div>
              <div>
                <v-icon v-if="filter.isChecked" color="#7fb901">mdi-check</v-icon>
              </div>
            </div>
            <v-divider></v-divider>
          </div>
        </v-card-text>
        <v-card-actions class="justify-end"></v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>
<script>import {mapGetters} from "vuex";

export default {
  name: 'DialogFilter',
  props: {
    filterSelected: {
      type: Function
    },
  },
  data(){
    return {
      dialog: false
    }
  },
  computed: {
    ...mapGetters({
          allChecked: `base/listJenisTanggapanAllChecked`,
          listFilter: `base/listPenyiarDetailKontak`,
          detailKontak: `base/detailKontak`,
        }
    )
  },
  methods: {
    filterClicked(filter) {
      this.listFilter.forEach(x => x.isChecked = false);
      filter.isChecked = true;
      this.$store.commit(`base/listPenyiarDetailKontak`, this.listFilter);
      this.filterSelected(filter);
      this.dialog = false;
    },
  },
  mounted() {
    // console.log(this.listFilter)
  }

}</script>
<style scoped>html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}</style>