<template>
  <div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row no-gutters>
          <v-col class="d-flex">
            <DialogFilter
                :filter-all="filterAll"
                :filter-selected="filterSelected"
            />
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col><h1 class="white--text">Telepon/Surat</h1></v-col>
        </v-row>
        <v-row no-gutters>
          <v-col>
            <v-text-field v-model="search" background-color="6c9d00" class="rounded-l text--white mainSearch" clearable
                          dense
                          filled label="Search"
                          solo-inverted
                          @input="filterNamaPemilikusaha()" @click:clear="filterNamaPemilikusaha">
              <template v-slot:prepend-inner="">
                <v-icon color="white">mdi-magnify</v-icon>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-card>
        <v-container v-if="!listPenyiar"><h1>Sedang mengunduh data untuk pertama kali, mohon menunggu...</h1></v-container>
        <v-container v-if="lastTelepon && lastTelepon.namaPemilikusaha" fluid>
          <v-row>
            <v-col class="d-flex"><h3 class="text--darken-1">TELEPON</h3>
              <h3 class="ml-auto green--text text--darken-1" style="cursor:pointer"
                  @click="goToTelepon">See All
              </h3>

            </v-col>
          </v-row>
          <v-row @click="goToDetailKontak(lastTelepon)" style="cursor: pointer;">
            <v-col>
              <v-row>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-medium">
                  {{ lastTelepon.namaPemilikusaha }}</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-regular">TELEPON</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="grey--text font-weight-regular">
                  {{ formatDate(lastTelepon.tanggalDikerjakan) }}</h4>
                </v-col>
              </v-row>
            </v-col>
            <v-col class="ml-auto mr-2" style="text-align: end">
              <v-avatar color="#7fb901"
                        size="48"
              >
                <v-icon>mdi-phone-in-talk</v-icon>
              </v-avatar>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-container>
        <v-container v-if="lastSurat && lastSurat.namaPemilikusaha" fluid>
          <v-row>
            <v-col class="d-flex"><h3 class="text--darken-1">SURAT</h3>
              <h3 class="ml-auto green--text text--darken-1" style="cursor:pointer"
                  @click="goToSurat">See All
              </h3>

            </v-col>
          </v-row>
          <v-row @click="goToDetailKontak(lastSurat)" style="cursor: pointer;">
            <v-col>
              <v-row>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-medium">
                  {{ lastSurat.namaPemilikusaha }}</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-regular">SURAT</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="grey--text font-weight-regular">
                  {{ formatDate(lastSurat.tanggalDikerjakan) }}</h4>
                </v-col>
              </v-row>
            </v-col>
            <v-col class="ml-auto mr-2" style="text-align: end">
              <v-avatar color="orange darken-2"
                        size="48"
              >
                <v-icon>mdi-email-fast-outline</v-icon>
              </v-avatar>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-container>
      </v-card>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import DialogFilter from "@/views/Home/DialogFilter";
import {mapGetters} from "vuex";

export default {
  name: "Home",
  data() {
    return {
      listDataDinas: [],
      allChecked: true,
      search: "",
    }
  },
  computed: {
    ...mapGetters({
      lastTelepon: 'base/lastTelepon',
      lastSurat: 'base/lastSurat',
      listPenyiar: 'base/listPenyiar',
    })
  },
  watch: {
    // lastTelepon(value) {
    //   console.log("lastTelepon")
    //   console.log(value)
    // }
  },
  methods:
      {
        goToDetailKontak(kontak) {
          this.$store.commit("base/detailKontak", kontak);
          this.$router.push('/detail-kontak')
        },
        goToTelepon() {
          this.$store.commit("base/titleTeleponSurat", "TELEPON");
          this.$router.push('/list-telepon-surat')
        },
        goToSurat() {
          this.$store.commit("base/titleTeleponSurat", "SURAT");
          this.$router.push('/list-telepon-surat')
        },
        filterSelected() {
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let penyiarTerpilih = this.listPenyiar.filter(x => x.isChecked).map(x => x.name);
            let loadDataDinasPenyiar = [];
            if (this.search) {
              loadDataDinas = loadDataDinas.filter(x => x.namaPemilikusaha.includes(this.search));
            }
            penyiarTerpilih.forEach(x => {
              loadDataDinasPenyiar = [...new Set([...loadDataDinasPenyiar, ...loadDataDinas.filter(xx => xx.name === x)])];
            });
            let loadDataDinasBelumDikerjakan = loadDataDinasPenyiar.filter(x => x.tanggalDikerjakan === "-");
            loadDataDinasPenyiar = loadDataDinasPenyiar.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            })
            loadDataDinasPenyiar = [...new Set([...loadDataDinasPenyiar, ...loadDataDinasBelumDikerjakan])];

            this.$store.commit("base/lastTelepon", loadDataDinasPenyiar.filter(x => x.jenis === "TELEPON").shift() ?? {});
            this.$store.commit("base/lastSurat", loadDataDinasPenyiar.filter(x => x.jenis === "SURAT").shift() ?? {});
          }
        },
        filterAll() {
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            if (this.search) {
              loadDataDinas = loadDataDinas.filter(x => x.namaPemilikusaha.toLowerCase().includes(this.search.toLowerCase()));
            }
            let loadDataDinasBelumDikerjakan = loadDataDinas.filter(x => x.tanggalDikerjakan === "-"); 
            loadDataDinas = loadDataDinas.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            })
            loadDataDinas = [...new Set([...loadDataDinas, ...loadDataDinasBelumDikerjakan])];

            this.$store.commit("base/lastTelepon", loadDataDinas.filter(x => x.jenis === "TELEPON").shift() ?? {});
            this.$store.commit("base/lastSurat", loadDataDinas.filter(x => x.jenis === "SURAT").shift() ?? {});
          }
        },
        filterNamaPemilikusaha() {
          this.$store.commit("base/searchMain", this.search);
          if (this.allChecked) this.filterAll();
          else this.filterSelected();
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
      },
  async mounted() {
    moment.locale('id');
  },
  components: {DialogFilter}
};
</script>
<style scoped>
html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}
</style>
