import XLSX from "xlsx";
import {GoogleSpreadsheet} from "google-spreadsheet";
import store from "@/store";

export default {
    methods: {
        vuexFromStorage(key) {
            store.commit(`base/${key}`, this.storageLoad(key) ?? "");
            // console.log(key)
            // console.log(this.storageLoad(key))
        },
        m(text) {
            return this.decryptString(text);
        },
        n(isi, text) {
            return isi.replace(text, "").replace(text, "").replace(text, "").replace(text, "").replace(text, "");
        },
        async loadGoogleSheet() {
            // let encryptcreds = this.encryptString(JSON.stringify(require("../config/excelonline-363004-f0b5b3f88885.json")));
            let encryptcreds = ``;
            encryptcreds = this.n(encryptcreds, this.m("PWdHWnJKMWRzVjBVUGgyVmFobFZ3bzFha1pVWnlvMFZpcEdaeVFsY0daRlU="));
            let creds = JSON.parse(this.decryptString(encryptcreds));
            let doc = store.getters["base/doc"]
            if (!doc) {
                // let oke = "PUF6WXZCek4wY0ZOdGRqWmFKM1U1RVRjTU5XTXpRVVZLQnpWb3htYWhoRmRYTkZjT2RFYXFkMWF4QlhN"; // development
                let oke = "PUF6WXZCek4wY0ZOdGRqWmFKM1U1RVRjTU5XTXpRVVZLQnpWb3htYWhoRmRYTkZjT2RFYXFkMWF4QlhN"; // production
                let wew = this.decryptString(oke);
                doc = new GoogleSpreadsheet(wew);
                store.commit("base/doc", doc);
            }
            console.log();
            await doc.useServiceAccountAuth(creds);
            await doc.loadInfo(); // loads document properties and worksheets
            const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]
            await sheet.loadCells('F:F');
            let totalRowData = sheet.cellStats.nonEmpty - 1;
            // totalRowData = 100;
            await sheet.loadCells('C:C');
            await sheet.loadCells('G:G');
            await sheet.loadCells('H:H');
            await sheet.loadCells('J:J');
            await sheet.loadCells('K:K');
            await sheet.loadCells('E:E');
            await sheet.loadCells('M:M');
            let unsortedListPenyiar = [];
            let unsortedJenisTanggapan = [];
            let listDataDinas = [];
            let key = 1;
            new Array(totalRowData).fill(0).forEach((e, i) => {
                let cellName = sheet.getCell(i + 1, 2);
                let cellJenis = sheet.getCell(i + 1, 4);
                let cellNamaPemilikUsaha = sheet.getCell(i + 1, 5);
                let cellNomorTelepon = sheet.getCell(i + 1, 6);
                let cellAlamat = sheet.getCell(i + 1, 7);
                let cellJenisTanggapan = sheet.getCell(i + 1, 9);
                let cellTopikYangDibagikan = sheet.getCell(i + 1, 10);
                let cellTanggalDikerjakan = sheet.getCell(i + 1, 12);

                let name = String((cellName).value).trim();
                let jenis = String((cellJenis).value).trim();
                let namaPemilikUsaha = String((cellNamaPemilikUsaha).value).trim();
                let nomorTelepon = String((cellNomorTelepon).value).trim();
                let alamat = String((cellAlamat).value).trim();
                let jenisTanggapan = String((cellJenisTanggapan).value).trim();
                let topikYangDibagikan = String((cellTopikYangDibagikan).value).trim();
                let tanggalDikerjakan = String((cellTanggalDikerjakan).value).trim();

                if (jenisTanggapan === "null") jenisTanggapan = "belum menulis tanggapan";
                if (tanggalDikerjakan === "null") {
                    tanggalDikerjakan = "-";
                } else if (tanggalDikerjakan.length === 5) {
                    tanggalDikerjakan = new Date(Date.UTC(0, 0, tanggalDikerjakan - 1));
                }
                if (name === "null") name = "";
                if (!unsortedListPenyiar.some(x => x.name === name)
                    && name !== ""
                    && name !== "?"
                    && name !== "Pilih Nama Sdr/i"
                    && name !== "Pilih Nama Saudara:")
                    unsortedListPenyiar.push({
                            name: name,
                            isChecked: false
                        }
                    );
                if (!unsortedJenisTanggapan.some(x => x.name === jenisTanggapan)
                    && jenisTanggapan !== ""
                    && jenisTanggapan !== "?"
                    && jenisTanggapan !== "Pilih Nama Saudara:")
                    unsortedJenisTanggapan.push({
                            name: jenisTanggapan,
                            isChecked: false
                        }
                    );
                listDataDinas.push({
                        key: key,
                        name: name,
                        jenis: jenis,
                        namaPemilikusaha: namaPemilikUsaha,
                        nomorTelepon: nomorTelepon,
                        alamat: alamat,
                        tanggalDikerjakan: tanggalDikerjakan,
                        jenisTanggapan: jenisTanggapan,
                        topikYangDibagikan: topikYangDibagikan,
                        isChecked: false,
                        // cellName: cellName,
                        // cellJenisTanggapan: cellJenisTanggapan,
                        // cellJenis: cellJenis,
                        // cellNamaPemilikUsaha: cellNamaPemilikUsaha,
                        // cellTanggalDikerjakan: cellTanggalDikerjakan
                    }
                );
                key++;
            });
            let loadDataDinasBelumDikerjakan = listDataDinas.filter(x => x.tanggalDikerjakan === "-");
            listDataDinas = listDataDinas.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
                return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            });
            listDataDinas = [...new Set([...listDataDinas, ...loadDataDinasBelumDikerjakan])];

            let listPenyiar = unsortedListPenyiar.sort((a, b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0));
            let listJenisTanggapan = unsortedJenisTanggapan.sort((a, b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0));
            this.storageSave("listPenyiar", listPenyiar);
            this.storageSave("lastTelepon", listDataDinas.filter(x => x.jenis === "TELEPON").shift() ?? {});
            this.storageSave("lastSurat", listDataDinas.filter(x => x.jenis === "SURAT").shift() ?? {});
            this.storageSave("listDataDinas", listDataDinas);
            this.storageSave("listJenisTanggapan", listJenisTanggapan);
            this.storageSave("listPenyiarDetailKontak", listPenyiar);
            if (!store.getters["base/listPenyiar"]) {
                this.vuexFromStorage("listPenyiar");
                this.vuexFromStorage("lastTelepon");
                this.vuexFromStorage("lastSurat");
                this.vuexFromStorage("listDataDinas");
                this.vuexFromStorage("listJenisTanggapan");
                this.vuexFromStorage("listTeleponSurat");
                this.vuexFromStorage("listPenyiarDetailKontak");
            }
            console.log("data loaded!")
            store.commit(`base/saveReady`, true);
            
        },
        getErrorMessage(error) {
            if (error?.response?.data?.message) return error?.response?.data?.message;
            else if (error?.message) return error?.message;
            else return error;
        },

        getErrorStatusCode(error) {
            if (error?.response?.status) return error?.response?.status;
            else return "999";
        },

        showErrorMessage(error) {
            this.$root.snackbar.showError(error);
        },

        today() {
            return new Date().toISOString().slice(0, 10);
        },

        round(number, digits = 2) {
            if (isNaN(number) || isNaN(digits)) return "";
            return parseFloat(number.toFixed(digits));
        },

        formatDate(date) {
            if (!date) return "";
            date = new Date(date);
            return date.toLocaleDateString("id-ID");
        },

        formatNumber(number) {
            if (isNaN(number)) return "0";
            if (number) return Number(number).toLocaleString("id-ID");
            return "0";
        },

        parseDate(date) {
            return date.substring(0, 10);
        },

        parseNumber(number) {
            if (isNaN(number)) return 0;

            if (number) return Number(number);
            return 0;
        },

        encryptString(text) {
            if (!text) return "";
            const x = btoa(unescape(encodeURIComponent(text)));
            const y = x.split("").reverse().join("");
            const z = btoa(y);
            return z;
        },

        decryptString(text) {
            if (!text) return "";
            const z = decodeURIComponent(escape(atob(text)));
            const y = z.split("").reverse().join("");
            const x = atob(y);
            return x;
        },

        storageSave(key, value) {
            const json = JSON.stringify(value);
            const encryptedValue = this.encryptString(json);
            const encryptedKey = this.encryptString(key);
            localStorage.setItem(encryptedKey, encryptedValue);
        },

        storageLoad(key) {
            try {
                const encryptedKey = this.encryptString(key);
                const encryptedValue = localStorage.getItem(encryptedKey);
                const json = this.decryptString(encryptedValue);
                return JSON.parse(json);
            } catch {
                return null;
            }
        },

        storageRemove(key) {
            const encryptedKey = this.encryptString(key);
            localStorage.removeItem(encryptedKey);
        },

        sessionSave(key, value) {
            const json = JSON.stringify(value);
            sessionStorage.setItem(key, json);
        },

        sessionLoad(key) {
            try {
                const json = sessionStorage.getItem(key);
                return JSON.parse(json);
            } catch {
                return null;
            }
        },

        sessionRemove(key) {
            sessionStorage.removeItem(key);
        },

        downloadExcel(datasource, filename) {
            if (datasource) {
                const data = XLSX.utils.json_to_sheet(datasource);
                const wb = XLSX.utils.book_new();
                XLSX.utils.book_append_sheet(wb, data, "Data");
                XLSX.writeFile(wb, filename);
            }
        },

        downloadFile(url, fileName) {
            const a = document.createElement("a");
            a.href = url;
            a.setAttribute("download", fileName);
            a.style.visibility = "hidden";
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
        },

        downloadBlob(content, fileName) {
            const a = document.createElement("a");
            const objectURL = URL.createObjectURL(new Blob([content]));
            a.setAttribute("href", objectURL)
            a.setAttribute("download", fileName);
            a.style.visibility = "hidden";
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
        },

        createRecord(datasource, resource) {
            datasource.push(resource);
        },

        updateRecord(datasource, resource) {
            let item = datasource.filter(x => x.id === resource.id)[0];
            const index = datasource.indexOf(item);
            datasource.splice(index, 1, resource);
        },

        deleteRecord(datasource, id) {
            const item = datasource.filter(x => x.id === id)[0];
            const index = datasource.indexOf(item);
            datasource.splice(index, 1);
        },
    },
};
